import Link from "next/link"
import { Instagram, Facebook, Twitter } from "lucide-react"
import styled from "styled-components"

const FooterContainer = styled.footer`
  background-color: #e8e0d5;
  padding: 3rem 0;
`

const Container = styled.div`
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 1rem;
`

const Grid = styled.div`
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  
  @media (min-width: 768px) {
    grid-template-columns: repeat(3, 1fr);
  }
`

const Column = styled.div``

const ColumnTitle = styled.h3`
  font-size: 1.125rem;
  font-weight: 500;
  margin-bottom: 1rem;
`

const Description = styled.p`
  color: #404040;
  margin-bottom: 1rem;
`

const SocialLinks = styled.div`
  display: flex;
  gap: 1rem;
`

const SocialLink = styled(Link)`
  color: #404040;
  transition: color 0.2s;
  
  &:hover {
    color: #171717;
  }
`

const ScreenReaderOnly = styled.span`
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border-width: 0;
`

const Nav = styled.nav`
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
`

const NavLink = styled(Link)`
  color: #404040;
  text-decoration: none;
  transition: color 0.2s;
  
  &:hover {
    color: #171717;
  }
`

const Address = styled.address`
  font-style: normal;
  color: #404040;
  
  p {
    margin: 0;
  }
`

const Copyright = styled.div`
  border-top: 1px solid rgba(64, 64, 64, 0.3);
  margin-top: 2rem;
  padding-top: 2rem;
  text-align: center;
  color: #404040;
`

export default function Footer() {
  return (
    <FooterContainer>
      <Container>
        <Grid>
          <Column>
            <ColumnTitle>Arathi Rajesh</ColumnTitle>
            <Description>
              Cultural impressionist artist exploring the intersection of Indian heritage and Arab influences.
            </Description>
            <SocialLinks>
              <SocialLink href="#">
                <Instagram size={20} />
                <ScreenReaderOnly>Instagram</ScreenReaderOnly>
              </SocialLink>
              <SocialLink href="#">
                <Facebook size={20} />
                <ScreenReaderOnly>Facebook</ScreenReaderOnly>
              </SocialLink>
              <SocialLink href="#">
                <Twitter size={20} />
                <ScreenReaderOnly>Twitter</ScreenReaderOnly>
              </SocialLink>
            </SocialLinks>
          </Column>

          <Column>
            <ColumnTitle>Navigation</ColumnTitle>
            <Nav>
              <NavLink href="/">Home</NavLink>
              <NavLink href="/about">About</NavLink>
              <NavLink href="/gallery">Gallery</NavLink>
              <NavLink href="/contact">Contact</NavLink>
            </Nav>
          </Column>

          <Column>
            <ColumnTitle>Contact</ColumnTitle>
            <Address>
              <p>Dubai, UAE</p>
              <p>Email: info@arathirajesh.com</p>
              <p>Phone: +971 50 123 4567</p>
            </Address>
          </Column>
        </Grid>

        <Copyright>
          <p>&copy; {new Date().getFullYear()} Arathi Rajesh. All rights reserved.</p>
        </Copyright>
      </Container>
    </FooterContainer>
  )
}
